.ContactsForm {
  width: 280px;
  height: max-content;
  border: 2px solid #1a1a1a;
  padding: 20px;
}

.InputName {
  font-weight: 700;
}

.InputForm {
  width: 180px;
  height: 20px;
  margin: 10px 0 30px;
}

.Alert-enter-active {
  animation: task 800ms ease-in alternate-reverse;
}

.Alert-exit-active {
  animation: back 800ms ease-in;
}

@keyframes task {
  0% {
    transform: translateX(0);
  }

  30% {
    opacity: 0.7;
    transform: translateX(30px);
  }

  100% {
    opacity: 0;
    transform: translateX(-200px);
  }
}

@keyframes back {
  0% {
    transform: translateX(0);
  }

  30% {
    opacity: 0.7;
    transform: translateX(30px);
  }

  100% {
    opacity: 0;
    transform: translateX(-200px);
  }
}
